.backdrop {
  width: 100%;
  height: 100%;
  z-index: 20;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1), background-color .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.active {
  opacity: 1;
  visibility: visible;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100vw;
  height: 100%;
  z-index: 30;
  text-align: center;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1), opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%)scaleY(.1);
}

.modal-cross {
  color: #131313;
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 12px;
  right: 12px;
}

@media screen and (min-width: 768px) {
  .modal-cross {
    width: 24px;
    height: 24px;
  }
}

.modal-cross:hover, .modal-cross:focus {
  background-color: #ffe99f;
}

.modal-cross__icon {
  width: 20px;
  height: 20px;
  color: currentColor;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .modal-cross__icon {
    width: 24px;
    height: 24px;
  }
}

.modal-cross:hover .modal-cross__icon, .modal-cross:focus .modal-cross__icon {
  transform: scale(.8);
}

.modal.active {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%)scale(1);
}

